import {NavLink} from "react-router-dom";
import React, {useState} from "react";
import Button from "../../../common/Button/Button";
import style from "./Menu.module.css"
import logo from "./img/logo.svg";

export const Menu = () => {
    const [menuActive, setMenuActive] = useState(false)

    return (
        <div className={style.menu}>
            <div className={style.logo}>
                <img src={logo}/>
                <div> </div>
            </div>
            <Button className={style.buttonMenu} onClick={() => setMenuActive(!menuActive)}></Button>
            <div className={style.textAndButton}>
                <div className={`${menuActive ? style.menuActive : style.text}`}>
                    <div>
                        <NavLink to={'/main'}> </NavLink>
                        <NavLink to={'/about_us'}> </NavLink>
                        <NavLink to={'/concurs'}> </NavLink>
                        <NavLink to={'/about_festival'}> </NavLink>
                    </div>
                    <Button className={style.buttonTrees}></Button>
                </div>
            </div>
        </div>
    )
}