import {Window} from "./Window/Window";

export const Gardener = () => {

    return (
        <div className={style.gardener}>
            <div className={style.header}>
                <div className={style.title}> </div>
                <div className={style.text}>  -   -,     ,   , ,    , </div>
            </div>
            <Window/>
        </div>
    );
}